import React from 'react';
import Helmet from 'react-helmet';
import GatsbyLink from 'gatsby-link'
import _ from 'lodash'
import {Popover, Tag, Menu, Dropdown, Button, Icon, Collapse, Breadcrumb } from 'antd'
import PhotoGrid from 'react-photo-feed';
import 'react-photo-feed/library/style.css';


import {Container, Price, Button as MyButton, Alert, Row, Col, H3} from '../components/mystyle'

import PostTags from '../components/PostTags/PostTags';
import SocialLinks from '../components/SocialLinks/SocialLinks';
import SEO from '../components/SEO/SEO';
import config from '../../data/SiteConfig';
import Product from '../components/Product';

import '../scss/single-product.scss';

import {getCategoryName} from '../Utils'
import {LOCAL_STORAGE_KEY_VIEWED} from '../Utils/common'

export default class PostTemplate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      phiGiaoHang: 0,
      vitri: '--- Chn ---'
    }
  }
  
  componentDidMount() {
    const postNode = this.props.data.markdownRemark;
    const post = postNode.frontmatter;
    // Save to recent View
    const {title, cover, id, price, salePrice, tags, path, date, excerpt} = post;
    const RecentViewedProducts = JSON.parse(window.localStorage.getItem(LOCAL_STORAGE_KEY_VIEWED));
    const newRecentViewdProducts = _.unionBy(RecentViewedProducts, [{title, cover, id, price, salePrice, tags, path, date, excerpt}], 'id' );
    window.localStorage.setItem(LOCAL_STORAGE_KEY_VIEWED, JSON.stringify(newRecentViewdProducts));
  }
  _goBack = e => {
    this.props.history.goBack();
  }
  renderSizeContent = (size) => {
    switch (size) {
    case 2:
      return (
        <div className="pl-3">
          <ul>
            <li>2 tui</li>
            <li>Cao t 82-85 cm</li>
            <li>Nng t 13 n 13.5</li>
          </ul>
        </div>
      );
    case 3:
      return (
        <div className="pl-3">
          <ul>
            <li>3 tui</li>
            <li>Cao t 86-95 cm</li>
            <li>Nng t 13.5 n 15.5</li>
          </ul>
        </div>
      );
    case 4:
      return (
        <div className="pl-3">
          <ul>
            <li>4 tui</li>
            <li>Cao t 96.5-105.5 cm</li>
            <li>Nng t 15.5 n 17.5</li>
          </ul>
        </div>
      );
    case 5:
      return (
        <div className="pl-3">
          <ul>
            <li>5 tui</li>
            <li>Cao t 106.5-113 cm</li>
            <li>Nng t 17.5 n 19</li>
          </ul>
        </div>
      );
    default:
      return (
        <div className="pl-3">
          <ul>
            <li>2 tui</li>
            <li>Cao t 82-85 cm</li>
            <li>Nng t 13.5 n 15.5</li>
          </ul>
        </div>
      )
    }
  }
  renderSize = (size, index) => {
    return (
      <Popover key={index} placement="top" title="Dnh cho b" content={this.renderSizeContent(size)} trigger="hover">
        <MyButton color="secondary" outline>
          {size}
        </MyButton>
      </Popover>
    )
  }
  renderSizes = (sizes) => {
    return (
      <div className="sizes-row">
        <div className="d-flex justify-content-between">
          <div>Size:</div>
          <div><GatsbyLink to='size-chuan-kich-thuoc-quan-ao-tre-em'>Bng kch thc chun <i className="ion-arrow-right-c" /></GatsbyLink></div>
        </div>
        <div className="mt-3">
          {sizes.map((size, index) => this.renderSize(size, index))}
        </div>
      </div>
    )
  }
  handleMenuClick = e => {
    let phiGiaoHang = 0;
    let vitri = '--- Chn ---';
    switch (e.key) {
    case '1':
      phiGiaoHang = 20000;
      vitri = "Qun 1,3,5,6,8,10,11";
      break;
    case '2':
      phiGiaoHang = 30000;
      vitri = "Qun khc";
      break;
    default:
      phiGiaoHang = 30000;
      vitri = "Tnh khc";
      break;
    }
    this.setState({phiGiaoHang, vitri})
  }
  render() {
    const { slug } = this.props.pathContext;
    const postNode = this.props.data.markdownRemark;
    const post = postNode.frontmatter;
    if (!post.id) {
      post.id = slug;
    }
    if (!post.id) {
      post.category_id = config.postDefaultCategoryID;
    }
    // ALL THE PHOTOS
    const allPhotos = [];
    if (post.cover) allPhotos.push({id: 1, src: post.cover, bigSrc: post.cover});
    if (post.thumb1) allPhotos.push({id: 2, src: post.thumb1, bigSrc: post.thumb1});
    if (post.thumb2) allPhotos.push({id: 3, src: post.thumb2, bigSrc: post.thumb2});
    if (post.thumb3) allPhotos.push({id: 4, src: post.thumb3, bigSrc: post.thumb3});
    if (post.thumb4) allPhotos.push({id: 5, src: post.thumb4, bigSrc: post.thumb4});
    const menu = (
      <Menu onClick={this.handleMenuClick}>
        <Menu.Item key="1">Qun 1, 3, 5, 6, 8, 10, 11 - TP.HCM</Menu.Item>
        <Menu.Item key="2">Cc qun khc ca TP.HCM</Menu.Item>
        <Menu.Item key="3">Cc tnh khc</Menu.Item>
      </Menu>
    );
    const cateName = getCategoryName(post.category);
    return (
      <div className="single-product">
        <Helmet>
          <title>{`${post.title} | ${config.siteTitle}`}</title>
        </Helmet>
        <SEO postPath={slug} postNode={postNode} postSEO />
        <div style={{ backgroundColor: '#f7f7f7', paddingTop: 10, paddingBottom: 10, marginBottom: 20 }}>
          <Container fluid>
            <div className="d-flex justify-content-between">
              <Button role="button" onClick={this._goBack}>
                <i className="ion-android-arrow-back"/>
                <span style={{paddingLeft: 5}}>
                Quay li
                </span>
              </Button>
              <Breadcrumb>
                <Breadcrumb.Item><GatsbyLink to="/">Trang Ch</GatsbyLink></Breadcrumb.Item>
                <Breadcrumb.Item><GatsbyLink to={`/categories/${post.category}`}>{cateName}</GatsbyLink></Breadcrumb.Item>
                <Breadcrumb.Item>{post.title}</Breadcrumb.Item>
              </Breadcrumb>
            </div>
          </Container>
        </div>
        <div className="product type-product has-post-thumbnail">
          <Container fluid>
            <Row>
              <Col sm={8} xs={12}>
                <PhotoGrid columns={3} photos={allPhotos} />
              </Col>
              <Col sm={4} xs={12}>
                <div className="summary entry-summary">
                  <Tag color="#f50">{cateName}</Tag>
                  <div className="product_meta">
                    <PostTags tags={post.tags} />
                  </div>
                  <h1 className="product_title entry-title">{post.title}</h1>
                  <Price price={post.price} salePrice={post.salePrice}/>
                  <Collapse style={{marginBottom: 20}}>
                    <Collapse.Panel header="Tnh chi ph vn chuyn" key="1">
                      <Alert color="success">
                        <b>Min ph giao hng</b> cho n hng c gi tr t <b>500.000</b>
                      </Alert>
                      <div className="d-flex justify-content-between">
                        <div>
                          Giao hng ti
                        </div>
                        <div>
                          <Dropdown overlay={menu}>
                            <Button>
                            {this.state.vitri}<Icon type="down" />
                            </Button>
                          </Dropdown>
                        </div>
                      </div>
                      <hr/>
                      <div className="d-flex justify-content-between">
                        <div>
                          Ph Vn Chuyn
                        </div>
                        <div>
                          <h3>{this.state.phiGiaoHang.toLocaleString()} </h3>
                        </div>
                      </div>
                    </Collapse.Panel>
                  </Collapse>
                  {!_.isEmpty(post.sizes)
                    ? this.renderSizes(post.sizes)
                    : null
                  }
                  <MyButton block size="lg" color="warning" style={{marginBottom: 30}}>
                    <span style={{display: 'block', fontSize: 12}}>
                      NHN TIN FACEBOOK
                    </span>
                    <span>
                       MUA HNG
                    </span>
                  </MyButton>
                  <div className="block-contact">
                    <div className="d-flex">
                      <a className="contact-facebook" href="#">
                        <i className="ion-social-facebook" />
                        <span>Chia s Facebook</span>
                      </a>
                      <a className="contact-chat" href="#">
                        <i className="ion-chatbubbles" />
                        <span>Gi tin nhn</span>
                      </a>
                    </div>
                    <div className="contact-phone">
                      <span>Gi t mua</span> <a href="">{config.phone}</a> (9h:00 - 21:00)
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
          <Container>
            <div className="single-post-container">
              <div className="post-content" dangerouslySetInnerHTML={{ __html: postNode.html }} />
            </div>
          </Container>
        </div>
      </div>
    );
  }
}

/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      excerpt
      frontmatter {
        title
        cover
        thumb1
        thumb2
        thumb3
        thumb4
        mau
        sizes
        date
        category
        tags
        price
        salePrice
      }
      fields {
        nextTitle
        nextSlug
        prevTitle
        prevSlug
        slug
      }
    }
  }
`;



